<template>
    <div class="box">
        <van-search
        placeholder="搜索"
        input-align="center"
        @focus="search"
        />
        <div class="swiper">
            <van-swipe :autoplay="3000" :loop="true" indicator-color="#fff">
                <van-swipe-item v-for="(image, index) in swipe" :key="index" @click="goGoods(image.goods_id)">
                <img class="auto-img" :src="image.image_src">
                </van-swipe-item>
            </van-swipe>
        </div>
        <div class="floor">
            <ul v-for="(item,index) in floors" :key="index">
                <li>
                    <div class="floorImg">
                        <img :src="item.floor_title.image_src" alt="">
                    </div>
                    <div class="floorText">{{item.floor_title.name}}</div>

                    <div class="falsh">
                        <ul>
                            
                                <li class="fl"  v-for="(pro,dur) in item.product_list" :key="dur">
                                    <div class="falshImg">
                                        <img class="auto-img" :src="pro.image_src" alt="">
                                    </div>
                                    {{pro.name}}
                                </li>
               
                        </ul>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import "../../assets/css/sort.css"
export default {
    data(){
        return{
            swipe:'',
            floors:'',
            cat_id: 929
        }
    },
    created(){
        this.floor();
        this.swiper();
        
    },
    methods: {
        gets(cat_id) {
            this.$router.push({ name: "SuperMarket"});
        },
       search(){
           this.$router.push('/serch')
       },

        floor(){
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0
            });
            this.axios({
                method: 'GET',
                url: '/home/floordata',
                data:{}
            }).then(result => {
                if(result.status == 200){
                    this.$toast.clear();
                    this.floors = result.data.message;
                }
                console.log('123',result);
            }).catch(err => {
                this.$toast.clear();
            })
        },
        
        swiper(){
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0
            });
            this.axios({
                method: 'GET',
                url: '/home/swiperdata',
                data:{}
            }).then(result => {
                if(result.status == 200){
                    this.$toast.clear();
                    this.swipe = result.data.message;
                    
                }
            }).catch(err => {
                this.$toast.clear();
            })
        },
        goGoods(aid){
            this.$router.push({name: "Particulars",query: { aid }})
        },
    }
}
</script>